import { useEffect, useLayoutEffect, useState } from 'react';
import Services from './services'
import './App.css';
import { AddPerson } from './components/AddPerson';
import { Filter } from './components/Filter';
import { Phonebook } from './components/Phonebook';
import Axios from 'axios';
import { render } from '@testing-library/react';
import { Alert } from './components/Alerts';
import { Messages } from './components/Messages';
import { Process } from './components/Process';


function App() {

  //initializing states states
  const [persons, setPersons] = useState(null)
  const [filteredPersons, setFilteredPersons] = useState(persons)

  const [personName, setName] = useState('')
  const [personNumber, setNumber] = useState('')

  const [alert, setAlert] = useState('')
  const [message, setMessage] = useState('')
  const [process, setProcess] = useState('')

  const [filter, setFilter] = useState('')
  

  //a function that handle c=the changes on input fields
  const handleChange = (e, setChange) => {
    setChange(e.target.value)
  }

  const filterPersons = () => {
    setFilteredPersons(persons.filter(person => person.name.toLowerCase().includes(filter.toLowerCase())))
  }

  useEffect(() => {
    Services.getAll()
      .then(response => setPersons(response.data))
  })

  
  // a function that handles pop up of messages and alerts
  const handlePop = (pop, setPop) => {
    setPop(pop)
    setTimeout(() => {
      setPop('')
    }, 2500)
  }


  //a function for adding new persons
   const addPerson = (e) => {

    const newPerson = {
      name: personName,
      number: personNumber,
    }

    e.preventDefault()

    if(personName === ''|| personNumber === '') {
      handlePop('At least one field is empty', setAlert)
    }

    else if (persons.find(person => person.name === newPerson.name))  {
      window.confirm(`A person with the name ${newPerson.name} already exists, would you like to update?`)
      {
        const personToChange = persons.find(person => person.name === newPerson.name)
        
        const newPersons = persons.map(person => person.name === newPerson.name ? newPerson : person)
        setPersons(newPersons)

        Services.updatePerson(personToChange.id, newPerson)
          .then(response =>  console.log(response.data))
        console.log(newPersons)
        handlePop(`${newPerson.name} has been updated`, setMessage)

      }
    }


    else  {
      console.log(newPerson)
      setPersons(persons.concat(newPerson))
      Services.addPerson(newPerson)
      handlePop(`${newPerson.name} has been added`, setMessage)

      setNumber('')
      setName('')

    }
  }

  const handleDelete = (id) => { 
    console.log(id)
    const personName = persons.find(person => person.id === id).name
    if (window.confirm(`Are you sure you want to delete ${personName}?`)){
    Services.deletePerson(persons.find(person => 
      person.id === id
    ).id)
    setPersons(persons.filter(person =>
      person.id !== id
    ).id)
    handlePop(`${personName} has been deleted`, setProcess)
  } else return null
  }

  const handleFilter = (e) => {
    setFilter(e.target.value)
    console.log(filter)

    filterPersons()
  }


return (
    <div>
      <Alert alert = {alert}/>
      <Messages message={message} />
      <Process process={process} />

      <h1>Phonebook</h1>
      <Filter handleFilter = {handleFilter} filter = {filter}/>
      
      <h2>Add a new person</h2>
      <AddPerson 
        handleName={(e, setChange) => handleChange(e, setName)}
        handleNumber={(e, setChange) => handleChange(e, setNumber)}
        personName={personName}
        personNumber={personNumber}
        addPerson={addPerson}
      />

      <h2>Numbers</h2>
      <Phonebook
          persons = {filter === '' ? persons : filteredPersons}
          handleDelete={handleDelete}
          />
    </div>
  );
}

export default App;
