export const Phonebook = ({persons, handleDelete, id}) => {

    const mainRender = () => {
        if((persons)){return persons.length === 0 ? 'Add people' : persons.map((person, id) => 
                    <div key = {id}>
                      <span className = {'phonebook'}>
                        {id + 1}. {person.name}: {person.number}
                    </span>
                        <button onClick = {(id) => handleDelete(person.id)} className = {'delete'}>delete</button>
                    </div>)}
                    else return 'Failed to fetch people'
    }

    return (
        <div >
            {mainRender()}
        </div>
    )
}