export const AddPerson = ({handleName, personName, handleNumber, personNumber, addPerson}) => {
    return (
        <div>
            <form onSubmit = {addPerson}>
                name: <input value = {personName} onChange = {handleName}></input>
                <br></br>
                number: <input value = {personNumber} onChange = {handleNumber}></input>
                <br></br>

                <button>Add Person</button>
            </form>
        </div>
    )
}